body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-box{display:flex;flex-direction:column;padding:20px}.search-box__label{font-size:20px}.search-box__input{margin:20px 0;padding:10px;font-size:18px;border:1px gray solid;border-radius:15px;color:#000;outline:none}
.navbar__subcatg{padding-top:10px;padding-bottom:10px;font-weight:bold;font-size:16px}.navbar__subcatg__item{padding-top:10px;padding-bottom:10px}.navbar__subcatg__itemtitle{padding-top:10px;padding-bottom:10px;padding-left:20px;color:#e6dfbe;border-left:3px solid #b3c396}.menutext{padding-left:8px}.checkbox{width:20px;height:20px;margin-right:10px}
.navcontainer{display:flex;flex-direction:column;overflow:auto;min-height:600px;background-color:#676768;padding:20px;height:100%;color:#fff;font-family:"Source Sans Pro",sans-serif;letter-spacing:.025em}
.pane{width:100%;border-left:60px solid #c0bec4;border-left:60px solid #b3c396;padding:30px;font-family:"Source Sans Pro",sans-serif;letter-spacing:.005em;color:#444;text-align:center}.pane .pane__header{font-size:28px;padding-bottom:30px;font-weight:bold}.pane__image{height:200px;width:200px;padding:0px;margin:0px;float:right;margin-right:100px}.pane__text{text-align:left;padding:20px;padding-bottom:50px;font-size:16px}.pane__author{text-align:left;font-style:italic;padding-left:20px;padding-bottom:50px}.pane__icons{display:flex;justify-content:space-between}.pane__entercomment{padding-top:40px;display:flex}.pane__head{display:grid;grid-template-columns:5fr .5fr .5fr}.comments__section{padding-left:60px;text-align:left}.comments__section__row{padding-top:15px;padding-bottom:15px}.comment{display:flex;padding-top:15px;padding-bottom:15px}.comment__right{display:flex;flex-direction:column;padding-left:20px}.comment__right__about{display:flex;justify-content:space-between;align-items:baseline}.commentbox{border-radius:10px;padding:10px;border:1px solid #444;width:300px;height:30px}.postbutton{margin-top:10px;margin-left:30px;border-radius:5px;border:none;padding:5px;background-color:#727174;color:#fff;width:50px;height:25px}.commentby{font-size:16px;font-weight:bold}.commenton{color:#444;font-size:12px}.commenttext{font-size:14px}.likes{font-style:italic}.category__head{display:flex;flex-direction:column}.category__text{font-size:20px}
.readsection{display:flex}
.header{margin:0px;padding:0px;font-family:"Source Sans Pro",sans-serif;letter-spacing:.005em;color:#ebeae5;background-color:#3c6060}.header__main{padding:0px;margin:0px;padding-top:15px;text-align:center;letter-spacing:.1em;word-spacing:.3em}.header__tagline{margin:0px;text-align:right;margin-right:50px;letter-spacing:.1em;word-spacing:.2em;font-size:18px;padding:0px;padding-bottom:10px}
.main{background-color:#fff;margin:0px;padding:0px}.main__body{padding:50px;padding-top:20px}.main__body__top{display:flex}.main__caption{background-color:#000;color:#fff;font-family:"Source Sans Pro",sans-serif;letter-spacing:.005em;font-size:20px;word-spacing:.5em;border-radius:3%;width:450px;margin-bottom:170px;padding-top:20px;margin-left:100px;color:#fff;background-color:#474050}.main__caption__text{margin:10px;letter-spacing:.002em}.browsetitle{font-family:"Source Sans Pro",sans-serif;letter-spacing:.005em;color:#ebeae5;background-color:#3c6060;padding:5px;padding-left:15px;font-size:24px}.main__img{padding-left:50px;height:300px;width:300px}.main__body__catgs{display:flex;justify-content:space-around}.link{text-decoration:none}.tech{padding:20px;font-size:22px;font-weight:bold;color:#fff;background-color:#474050;border-radius:5px}.pers{padding:20px;font-size:22px;font-weight:bold;color:#474050;border-radius:5px;background-color:#c1bbc5}.hobb{padding:20px;font-size:22px;font-weight:bold;color:#fff;border-radius:5px;background-color:#b3c396}.comp{padding:20px;font-size:22px;font-weight:bold;color:#fff;color:#ebeae5;border-radius:5px;background-color:#3c6060}
.editor{padding:40px;padding-left:70px;background-color:#ebeae5}.editor__section{margin-top:30px;max-height:300px;min-height:300px;overflow:auto;padding-left:20px;background-color:#fff}.publishbutton{padding-top:20px;padding-bottom:30px}.writesubmit{padding:5x;background-color:#3c6060;color:#fff;border-radius:5px;line-height:30px;font-weight:bold}.fieldinput{display:flex;padding-bottom:5px;align-items:center}.fieldname{font-family:"Source Sans Pro",sans-serif;letter-spacing:.035em;font-size:16px;color:#3c6060;font-weight:bold;margin-right:10px}.inputbox{padding:5px;padding-right:10px;border:2px solid #ebeae5;border-radius:5%}.successtext{font-family:"Source Sans Pro",sans-serif;letter-spacing:.035em;font-size:26px;color:#3c6060;font-weight:bold;margin:50px}
.app{padding:0px;margin:0px}
